import React, { Component } from "react";
import { useNavigate } from "react-router-dom";
import "./Settings.css";

function Settings() {
  let navigate = useNavigate();
  const settings = {
    className: "center",
    infinite: true,
    centerPadding: "60px",
    slidesToShow: 5,
    swipeToSlide: true,
    afterChange: function (index) {
      console.log(
        `Slider Changed to: ${index + 1}, background: #222; color: #bada55`
      );
    },
  };

  return (
    <div className="settings-screen">
      <div className="slides">
        <div className="img_container">
          <img src="images/.jpg" />
        </div>
        <div className="img_container">
          <img src="images/.jpg" />
        </div>
        <div className="img_container">
          <img src="images/.png" />
        </div>
        <div className="img_container">
          <img src="images/.jpg" />
        </div>
        <div className="img_container">
          <img src="images/.jpeg" />
        </div>
        <div className="img_container">
          <img src="images/.jpg" />
        </div>
      </div>

      <div className="btn">
        <button id=""> </button>
      </div>

      <div className="settings_menus">
        <div className="settings_menu">
          <p> </p>
        </div>

        <div className="settings_menu">
          <p></p>
        </div>

        <div className="settings_menu">
          <p></p>
        </div>

        <div className="settings_menu">
          <p></p>
        </div>

        <div className="settings_menu">
          <p></p>
        </div>

        <div className="settings_menu">
          <p></p>
        </div>

        <div className="settings_menu">
          <p> </p>
        </div>
        <button
          id=""
          onClick={() => {
            navigate("/main");
          }}
          style={{ cursor: "pointer" }}
        >
          Home
        </button>
      </div>
    </div>
  );
}

export default Settings;
