import { useNavigate } from "react-router-dom";
import { useState } from "react";
import "./Subscribe.css";
import Modal from "react-modal";
import Confetti from "react-confetti";

function Subscribe() {
  let navigate = useNavigate();
  const [modalIsOpen, setModalIsOpen] = useState(false);

  return (
    <div className="subs-container">
      <div className="join-form__header">
        <div>
          <img id="" src="images/whitelogo.png" />
        </div>
        <div>
          <button
            className="login_button"
            onClick={() => {
              navigate("/login");
            }}
          >
            
          </button>
        </div>
      </div>

      <form className="subs-form">
        <div className="join-form__inputs">
          <h1 style={{ color: "white", marginBottom: "40px" }}>
            Welcome to dizplus
          </h1>

          <input type="text" placeholder="" />

          <input
            type="text"
            id="id"
            className="login"
            placeholder="   "
          />

          <input
            type="password"
            id="password"
            className="login"
            placeholder="  "
          />

          <input
            type="password"
            id="password"
            className="login"
            placeholder="    "
          />

          <div className="checkBoxes">
            <div className="checkbox1">
              <input
                style={{ width: "10px", height: "10px", marginRight: "5px" }}
                type="checkbox"
              />
              <p style={{ color: "white", fontSize: "10px" }}>
                  19  +  .
              </p>
            </div>
            <div className="checkbox2">
              <input
                style={{ width: "10px", height: "10px", marginRight: "5px" }}
                type="checkbox"
              />
              <p style={{ color: "white", fontSize: "10px" }}>
                +     .
              </p>
            </div>
          </div>

          <button
            id="finish"
            onClick={(e) => {
              e.preventDefault();
              setModalIsOpen(true);
            }}
          >
             
          </button>
        </div>
        <Modal
          className="Modal"
          isOpen={modalIsOpen}
          onRequestClose={() => setModalIsOpen(false)}
        >
          dizplus   <br /> !
          <button
            id="close"
            onClick={() => {
              navigate("/main");
            }}
          >
             
          </button>
          <Confetti />
        </Modal>
      </form>
    </div>
  );
}

export default Subscribe;
